
.sorting-container {
  display: flex;
  justify-content: left;
}

.sorting-dropdown {
  padding: 0.8em 2em;
  border-radius: 1em;
  border: 2px solid #5fe2b6;
  cursor: pointer;
}

option {
  border: 1px solid #aff5dd;
  cursor: pointer;
}

option:checked 
{ background-color: #aff5dd; }