.search-input {
  display: flex;
  justify-content: left;
  align-content: center;
  margin-top: 10px;
}

.search-input input {
  padding: 0.5em 2em;
  border-top-left-radius: 1em;
  border-bottom-left-radius: 1em;
  border: 2px solid #5fe2b6;
}

.search-input input:focus, 
.search-input input:active {
  border: 2px solid #bf0ecb;
  box-shadow: 0.2em 0.2em 0.5em -0.3em #bf0ecb;
}

.search-input:hover svg path {
  fill: #bf0ecb;
}

.search-icon {
  align-self: center;
  display: inline-block;
  margin-right: 20px;
  width: 20px;
  height: 20px;
  cursor: pointer;
  padding: 0.5em;
  border: 2px solid #5fe2b6;
  border-top-right-radius: 1em;
  border-bottom-right-radius: 1em;
  border-left: none;
}

.search-icon svg {
  width: 20px;
  height: 20px;
}
