.gender-list {
  margin-top: 10px;
  display: flex;
  justify-content: space-between;
}

.filter-gender__button {
  padding: 0.5em 2em;
  border-radius: 1em;
  border: 2px solid #5fe2b6;
  cursor: pointer;
}

.filter-gender__button-active {
  border: 2px solid #bf0ecb;
  box-shadow: 0.2em 0.2em 0.5em -0.3em #bf0ecb;
}
