.reset-button-container {
  display: flex;
  justify-content: left;
  align-content: center;
  margin-top: 10px;
}

.reset-filters__button {
  margin-top: 10px;
  padding: 0.5em 2em;
  border-radius: 1em;
  border: 2px solid #5fe2b6;
  cursor: pointer;
}

.reset-filters__button:hover {
  border: 2px solid #f56099;
  box-shadow: 0.2em 0.2em 0.5em -0.3em #f56099;
}