.filter-popular__button,
.filter-newProduct__button {
  margin-top: 10px;
  padding: 0.5em 2em;
  border-radius: 1em;
  border: 2px solid #5fe2b6;
  cursor: pointer;
  box-shadow: none;
}

.filter-popular__button-active,
.filter-newProduct__button-active {
  border: 2px solid #bf0ecb;
  box-shadow: 0.2em 0.2em 0.5em -0.3em #bf0ecb;
}